import React, {useState, useEffect} from 'react';
import axios, { formToJSON } from 'axios';
import { Link, useParams } from "react-router-dom";

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';

import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import ReactMarkdown from 'react-markdown'

import Url from "../api/url";

// Map config
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import "leaflet/dist/leaflet.css"
import "leaflet/dist/images/marker-icon.png"
import L from 'leaflet'

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});
// end map config

function Group(props) {
    const params = useParams();
    const [peopleList, setPeopleList] = useState([]);
    const [infraInGroup, setInfraInGroup] = useState([]);
    const [infraList, setInfraList] = useState([]);
    const [group, setGroup] = useState([]);
    const [id, setId] = useState();

    const [picList, setPicList] = useState([]);
    const [isLoadingGroup, setIsLoadingGroup] = useState(false);

    const headers = {
        Authorization: `Token ${localStorage.getItem("token")}`,
      };  

const getPeople = async() => {
        // axios.get(Url.baseUrl + `products/`, {headers: headers,})
        axios.get(Url.baseUrl + `people/?group=${params.id}`, {headers: headers,})
            .then((response) => {     
                let res = response.data.results;
                setPeopleList(res)
            })
            .catch((error) => {
                console.log(error);
            })
}

const getGroup = async() => {
      setIsLoadingGroup(true);
      axios.get(Url.baseUrl + `groups/${params.id}`, {headers: headers,})
          .then((response) => {     
              let res = response.data;
              setGroup(res)
              splitimages(res.images)
              setIsLoadingGroup(false);
              let infing = [];
              res.infrastructure.forEach((inf) => {
                infing.push(inf.id)
              })
              setInfraInGroup(infing)
              console.log('see: ', res)
          })
          .catch((error) => {
              console.log(error);
          })
}

const getInfra = async() => {
  // axios.get(Url.baseUrl + `products/`, {headers: headers,})
  axios.get(Url.baseUrl + `infrastructure/`, {headers: headers,})
      .then((response) => {     
          let res = response.data.results;
          console.log('infra: ', res)
          setInfraList(res)
      })
      .catch((error) => {
          console.log(error);
      })
}

const splitimages = (imgtxt) => {
  let arrimg = []
  const parts = imgtxt.split('\r\n')
  parts.forEach((part) => {
    arrimg.push(part)
  })
  setPicList(arrimg)
}


  useEffect(() => {     
        getPeople()
        getInfra()
        getGroup()
        setId(params.id)
}, []);


return (
<Container>
  {isLoadingGroup ? <div className="spinner_cont"><div className="lds-ripple"><div></div><div></div></div></div> : 
  <>
  <Row>
    <h1 className="d-flex justify-content-center">Village: {group.name}</h1>
  </Row>
  
  <Link to={`/groups/`} className='link'>
    <Button variant="success" style={{marginTop: "20px"}}>Back</Button> 
  </Link>
  
  <Row>
    <Col xs={12} md={8}>
      <Row>
          <Col><ReactMarkdown>{group.description}</ReactMarkdown></Col>
      </Row>
      <Row>
      <h1 id='moco'>Demography</h1>
      </Row>
      {(group.population || group.nunber_familes) &&
        <Row>
        <Col>
          <Table striped bordered hover>
          <thead>
            <tr>
              <th>Population</th>
              <th>Families</th>
            </tr>
          </thead>
          <tbody>        
            <tr>
              <td>{group.population}</td>
              <td>{group.nunber_familes}</td>
            </tr>
          </tbody>
        </Table>
        </Col>
      </Row>      
      }
      <Row>
        <Col>
          <Table striped bordered hover>
          <thead>
          {(group.children_female || group.children_male || group.youth_female || group.youth_male || group.adult_female || group.adult_male || group.old_female || group.old_male ) &&  
            <tr>
              <th></th>
              <th>Female</th>
              <th>Male</th>
            </tr>
          }
          </thead>
          <tbody>
          {(group.children_female || group.children_male) &&        
            <tr>
              <td>Children</td>
              <td>{group.children_female}</td>
              <td>{group.children_male}</td>
            </tr>  
          }
          {(group.youth_female || group.youth_male) &&      
            <tr>
              <td>Youth</td>
              <td>{group.youth_female}</td>
              <td>{group.youth_male}</td>
            </tr> 
          }
          {(group.adult_female || group.adult_male) &&                
            <tr>
              <td>Adults</td>
              <td>{group.adult_female}</td>
              <td>{group.adult_male}</td>
            </tr> 
          }
          {(group.old_female || group.old_male) &&    
            <tr>
              <td>Elderly</td>
              <td>{group.old_female}</td>
              <td>{group.old_male}</td>
            </tr>   
          }                            
          </tbody>
          </Table>
        </Col>
      </Row>   
      <Row>
      <h1>Infrastructure</h1>
      </Row>
      <Row>
        <Col>
          <Table striped bordered hover id="infra_table" className='firstcol'>
          <tbody>      
            {infraList.map((item, index)=>{
              return(
                <tr>
                  <td>{item.name}</td>
                  {item.inf.map((iteminf, index)=>{
                    return(
                      <td className={infraInGroup.includes(iteminf.id) ? 'mycolor': null} >{iteminf.name}</td>
                    )
                  })}
                </tr>
              )
            })}         
          </tbody>

        </Table>
        </Col>
      </Row> 
      {group.enrollments && group.enrollments.length > 0 && 
      <>
      <Row>
      <h1>Courses</h1>
      </Row>
      <Row>
        <Col>
          <Table striped bordered hover id="infra_table" className='firstcol'>
          <thead>
            <tr>
              <th>Course</th>
              <th>Date</th>
              <th>Duration (weeks)</th>
              <th>Participants</th>
            </tr>
          </thead>  
          <tbody>          
            {group.enrollments.map((item, index)=>{
              return(
                <tr>
                  <td>{item.course}</td>
                  <td>{item.date}</td>
                  <td>{item.duration_weeks}</td>
                  <td>{item.participants}</td>
                </tr>
              )
            })}            
          </tbody>
        </Table>
        </Col>
      </Row> 
      </>
      }  

      <Row>
        <Col>
        {/* hide this block by inserting false in the if statement below */}
        {group.lat && group.long && false && 
          <div style={{border: 'black 1px solid'}}>
          <MapContainer
            center={[group.lat, group.long]}
            zoom={13}
            scrollWheelZoom={true}
            style={{ height: '50vh', width: '100wh' }}
            >
            <TileLayer
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              />
            <Marker position={[group.lat, group.long]}>
              <Popup>
                <h2>{group.name}</h2>
              </Popup>
            </Marker>
            <Marker position={[group.lat-1, group.long-1]}>
              <Popup>
                <h2>{group.name}</h2>
              </Popup>
            </Marker>
          </MapContainer>
          </div>
        }
        </Col>
      </Row>
      {/* Google maps 'z' controls the zoom */}
      <Row style={{marginTop: '15px'}}>
        <Col>
        {group.lat && group.long && 
          <iframe width="100%" height="500" src={`https://maps.google.com/maps?q=${group.lat}, ${group.long}&output=embed&z=12`}></iframe>
        }
        </Col>
      </Row>
  
    </Col>
    <Col xs={12} md={4}>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Name</th>
            <th>Surname</th>
          </tr>
        </thead>
        <tbody>        
          {peopleList.map((item, index)=>{
            return(
              <tr key={index}>
                    <td>
                        <Link to={`/people/${item.pk}`} className='link'>{item.name} {item.surname}</Link>
                    </td>
                    <td>{item.surname}</td>
                </tr>  
            )
          })}
        </tbody>
      </Table>
    </Col>
  </Row>

  </>
  }


{picList[0] &&
<Row>
    <Carousel useKeyboardArrows={true}>
        {picList.map((URL, index) => (
          <div className="slide">
            <img alt="sample_file" src={URL} key={index} />
          </div>
        ))}
    </Carousel>
</Row>
}

</Container>
);
}

export default Group;
